// Fonts
@import url('https://fonts.googleapis.com/css?family=Biryani|Muli|Prompt|Open+Sans|Roboto+Mono|Work+Sans:800');

// Variables
@import "variables";

a {
    text-decoration: none;
    color: inherit; }

body {
    background-color: #fff;
    font-family: 'Muli', sans-serif;
    overflow-y: auto;
    overflow-x: hidden;
    margin: 0;
    padding: 0; }

nav {
    width: 250px;
    height: 100vh;
    position: fixed;
    z-index: 10;
    background-color: $nav;
    box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);

    .logo {
        height: 250px;
        width: 250px;
        color: white;

        display: flex;
        justify-content: center;
        align-items: center;

        .cursor {
            color: $red; }

        .logo-text {
            font-size: 15vh;
            color: #272727;
            position: absolute;
            z-index: -1; }

        .logo-name {
            color: white;
            position: absolute;
            display: flex;
            flex-direction: column;
            text-decoration: none;

            #name-text {
                font-size: 4.5vh;
                margin-bottom: -5px; }

            #name-first {
                font-size: 1.5vh; }

            #name-last {
                font-size: 1.5vh; }

            &:hover {
                color: $red;
                transition: color .1s ease-in;
                cursor: pointer; } } }


    .navigation {
        text-align: center;
        color: white;
        width: 100%;
        height: 50%;
        margin: 0 auto;

        a {
            margin-bottom: 50px; }

        .link {
            cursor: pointer;
            position: relative;
            height: 50px;

            li {
                list-style-type: none;
                display: flex;
                justify-content: center;
                align-items: center;
                height: 50px; }


            span:nth-child(1) {
                position: absolute;
                height: 5px;
                width: 0%;
                top: 0;
                left: 0;
                background-color: $red;
                transition: width .2s ease-in-out; }

            span:nth-child(3) {
                position: absolute;
                height: 5px;
                width: 0%;
                bottom: 0;
                right: 0;
                background-color: $red;
                transition: width .2s ease-in-out; }

            &:hover {
                span:nth-child(1) {
                    position: absolute;
                    height: 5px;
                    width: 100%;
                    top: 0;
                    left: 0;
                    background-color: $red;
                    transition: width .2s ease-in-out; }

                span:nth-child(3) {
                    position: absolute;
                    height: 5px;
                    width: 100%;
                    bottom: 0;
                    right: 0;
                    background-color: $red;
                    transition: width .2s ease-in-out; } } } }

    .links {
        width: 100%;
        height: 50%; } }

.container {
    width: calc(100% - 250px);
    margin-left: 250px;
 }    //background: linear-gradient(#fff, #DDE3EA);

.blog-sidebar {
    position: fixed;
    z-index: 5;
    height: 100vh;
    width: 34.35%;
    margin-left: -34.35%;
    background-color: #222;
    color: white;

    a:hover .blog-sidebar {
        margin-left: 0; }

    .blog-sidebar-content {
        padding: 10px 10px 10px 270px; }

    &:hover {
        margin-left: 0; }

    &.out {
        margin-left: 0;
        animation: enter .2s ease-in-out; }

    &.leave {
        margin-left: -34.35%;
        animation: leave .2s ease-in-out; } }

@keyframes fill {
    0% {
        height: 5px;
        color: white; }

    100% {
        height: 25px;
        color: white; } }

@keyframes enter {
    0% {
        margin-left: -34.35%; }
    100% {
        margin-left: 0; } }

@keyframes leave {
    0% {
        margin-left: 0; }
    100% {
        margin-left: -34.35%; } }

@import "index";
@import "post";
